import React from "react";
import data from "../data/data";
import CSSLogo from "../media/CSSLogo.png";
import EclipseLogo from "../media/EclipseLogo.png";
import GitHubLogo from "../media/GitHubLogo.png";
import GitLogo from "../media/GitLogo.png";
import GraphQLLogo from "../media/GraphQLLogo.png";
import HTMLLogo from "../media/HTMLLogo.png";
import JavaLogo from "../media/JavaLogo.png";
import JavaScriptLogo from "../media/JavaScriptLogo.png";
import NodeLogo from "../media/NodeLogo.png";
import ReactLogo from "../media/ReactLogo.png";
import SpringBootLogo from "../media/SpringBootLogo.png";
import SQLLogo from "../media/SQLLogo.png";
import TrelloLogo from "../media/TrelloLogo.png";
import VisualStudioCodeLogo from "../media/VisualStudioCodeLogo.png";
import Experiences from "./Experiences";

const Affichages = (props) => {
  const logoComp = [JavaScriptLogo, ReactLogo, NodeLogo, GraphQLLogo, SQLLogo, JavaLogo,SpringBootLogo,HTMLLogo, CSSLogo];
  const logoOutil = [EclipseLogo, VisualStudioCodeLogo, GitLogo, GitHubLogo, TrelloLogo]
  switch (props.page) {
    case "formations":
      return (
        <div className="formationsCont">
          <ul>
            {data.formations.map((formation, index) => (
              <li key={index}>
                <p className="titre">{formation.titre}</p>
                <p className="date">{formation.date}</p>
                <p className="organisme">{formation.organisme}</p>
              </li>
            ))}
          </ul>
        </div>
      );
    case "experiences":
      return (
        <Experiences experiences={data.experiences} />
      );
    case "competences":
      return (
        <div className="competencesCont">
          <ul className="technologie">
            <p className="entete">Technologie:</p>
            {data.competences.technologie.map((techno, index) => (
              <li className="logo" key={index}>
                <img
                  src={logoComp[index]}
                  alt={techno}
                />
                <p className="techName">{techno}</p>
              </li>
            ))}
          </ul>
          <ul className="outils"> 
            <p className="entete">Outils:</p>
            {data.competences.outils.map((outil, index) => (
              <li className="logo" key={index}>
                <img src={logoOutil[index]} alt={outil} />
                <p className="outilName">{outil}</p>
              </li>
            ))}
          </ul>
        </div>
      );
    default:
      throw new Error("Page inexistante");
  }
};

export default Affichages;
