import React from 'react';

const Experiences = ( props ) => {
    return (
        <div className="experiencesCont">
          <ul>
            {props.experiences.map((job, index) => (
              <li key={index}>
                <p className="titre">{job.titre}</p>
                <p className="date">{job.date}</p>
                <p className="employeur">{job.employeur}</p>
                <p className="descriptif">{job.descriptif}</p>
              </li>
            ))}
          </ul>
          <div>
              
          </div>
        </div>
      );
};

export default Experiences;